
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">公共配置</el-tag>
        <el-form style="margin-top: 30px">
            <el-form-item label="中转耗时 (徐闻---北港码头)：" style="margin-left: 50px;">
                <el-input  v-model="midtime" auto-complete="off " style="width: 100px" placeholder=""></el-input> 分钟
                <el-button type="primary" @click="midtimesure"  style="margin-left: 40px">保存</el-button>
            </el-form-item>
        </el-form>
        <el-form style="margin-top: 30px">
            <el-form-item label="轮渡退票 (退票手续费配置)：" style="margin-left: 50px">
                <el-input  v-model="ferryrefund" auto-complete="off " style="width: 100px" placeholder=""></el-input> 元
                <el-button type="primary" @click="ferryrefundsure"  style="margin-left: 50px">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                midtime:"",
                ferryrefund:"",
                formLabelWidth:"120px"
            };
        },
        methods: {
            getConfig(){
                ajax.getIntervalTime({},res=>{
                    if(res){
                        this.midtime = res.data.result
                    }
                })
                ajax.getLunDuRefundFee({},res=>{
                    if(res){
                        this.ferryrefund = res.data.result
                    }
                })
            },
            midtimesure(){
                ajax.updateIntervalTime({value:this.midtime},res=>{
                    if(res){
                        this.$message({
                            showClose: true,
                            message: '保存成功',
                            type: 'success'
                        });
                    }
                })
            },
            ferryrefundsure(){
                ajax.updateLunDuRefundFee({value:this.ferryrefund},res=>{
                    if(res){
                        this.$message({
                            showClose: true,
                            message: '保存成功',
                            type: 'success'
                        });
                    }
                })
            }

        },
        mounted(){
            this.getConfig()

         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



